import Vue from 'vue'
import VueRouter from 'vue-router'
import P5List from '../views/P5List.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'P5List',
    component: P5List,
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
